import React, { useState } from "react";
import { connect } from "react-redux";
import {
  Button,
  DialogContentText,
  MobileStepper,
  Typography,
} from "@material-ui/core";
import MuiDialogTitle from "@material-ui/core/DialogTitle";
import MuiDialogContent from "@material-ui/core/DialogContent";
import MuiDialogActions from "@material-ui/core/DialogActions";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/Close";
import { KeyboardArrowLeft, KeyboardArrowRight } from "@material-ui/icons";
import { withStyles } from "@material-ui/core/styles";

const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: "absolute",
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton
          aria-label="close"
          className={classes.closeButton}
          onClick={onClose}
        >
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const DialogContent = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
    minHeight: "40vh",
  },
}))(MuiDialogContent);

const DialogActions = withStyles((theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(1),
  },
}))(MuiDialogActions);

const LogStage4 = (props) => {
  
  const [emotionLog, setEmotionLog] = useState({
    primaryEmotion: props.primaryEmotion,
    intensityEmotion: props.intensityEmotion,
    intensityLevel: props.intensityLevel,
    howFeel: props.howFeel,
    whyFeel: props.whyFeel,
  });

  const theEnd = () => {
    setEmotionLog({
      primaryEmotion: props.primaryEmotion,
      intensityEmotion: props.intensityEmotion,
      intensityLevel: props.intensityLevel,
      howFeel: props.howFeel,
      whyFeel: props.whyFeel,
    });
    console.log(emotionLog);

    props.handleCloseAndDispatch(emotionLog); 
  }

  return (
    <div>
      {/* ----- This is the start of the Dialog Area ----- */}
      <DialogTitle id="customized-dialog-title" onClose={props.handleClose}>
        Review Emotional Log
      </DialogTitle>
      <DialogContent dividers>
        <DialogContentText>Here is your log:</DialogContentText>
        <ul>
          <li>{props.primaryEmotion}</li>
          <li>{props.intensityLevel}</li>
          <li>{props.intensityEmotion}</li>
          <li>{props.howFeel}</li>
          <li>{props.whyFeel}</li>
        </ul>
      </DialogContent>
      <DialogActions>
        <MobileStepper
          variant="progress"
          steps={4}
          position="static"
          activeStep={props.activeStep}
          className={props.classes.root}
          nextButton={
            <Button
              size="small"
              onClick={theEnd}
              disabled={props.activeStep === 4}
            >
              Finish
              {props.theme.direction === "rtl" ? (
                <KeyboardArrowLeft />
              ) : (
                <KeyboardArrowRight />
              )}
            </Button>
          }
          backButton={
            <Button
              size="small"
              onClick={props.prevStage}
              disabled={props.activeStep === 0}
            >
              {props.theme.direction === "rtl" ? (
                <KeyboardArrowRight />
              ) : (
                <KeyboardArrowLeft />
              )}
              Back
            </Button>
          }
        />
      </DialogActions>
      {/* ----- This is the end of the Dialog Area ----- */}
    </div>
  );
};

export default connect()(LogStage4);
